
import Vue from 'vue'
import Tokenizer, { Constructor } from '@/tokenizer'
import testapikey from '@/docs/helpers/testapikey.ts'

export default Vue.extend({
  name: 'Payments',
  data() {
    return {
      example: (null as any),
      exampleResponse: ''
    }
  },
  mounted() {
    this.example = new Tokenizer({
      apikey: testapikey,
      container: this.$refs.example as HTMLDivElement,
      submission: (resp: any) => {
        this.exampleResponse = resp
      },
      settings: {
        payment: {
          showTitle: true,
          types: ['card', 'ach'],
          ach: {
            sec_code: 'web'
          }
        }
      }
    })
  }
})
